* {
  padding: 0;
  margin: 0;
  border: 0;
  text-decoration: none;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  font-family: "Lato", sans-serif;
}

html {
  font-size: 62.5%;

  img {
    width: 100%;
  }

  @include responsive(1280px) {
    font-size: 55%;
  }
}
